import { useNavigate } from "react-router-dom";

const HomeCategory = (props) => {
    const { type } = props;

    const Items = [
        {
            src: "logo_1.png",
            type: 'indo-western',
            height: '536px',
            width: '525px'
        },
       
        {
            src: "5.png",
            type: 'indo-western',
            height: '536px',
            width: '525px'
        },

        {
            src: "4.png",
            type: 'indo-western',
            height: '536px',
            width: '525px'
        },
        {
            src: "6.jpg",
            type: 'work-wear',
            height: '536px',
            width: '400px'
        },
        {
            src: "6.jpg",
            type: 'work-wear',
            height: '536px',
            width: '400px'
        },
        {
            src: "7.jpg",
            type: 'work-wear',
            height: '536px',
            width: '400px'
        },
        {
            src: "8.png",
            type: 'home-decor',
            height: '536px',
            width: '525px'
        },
        {
            src: "8.png",
            type: 'home-decor',
            height: '536px',
            width: '525px'
        },
        {
            src: "9.png",
            type: 'home-decor',
            height: '536px',
            width: '525px'
        }
    ]

    const navigate = useNavigate();

    return (
        
        <div className='row g-0 justify-content-between category-wrapper pt-3'>
            {Items.filter(ele => ele.type === type).map(data => (
                <div className="col-xxl-4 col-xl-4 col-lg-4 col-md-6 col-12 p-0 item-wrapper" onClick={()=>navigate("/products")}>                    
                    <img className="image-style" src={require(`../../assets/img/gallery/${data.src}`)} />
                </div>
            ))}
        </div>
    )
}

export default HomeCategory;