import React from "react";
import { UncontrolledCarousel, Row, Col } from "reactstrap";
import SliderImage from "../../assets/img/gallery/slider1.jpg";

const items = [
  {
    src:  SliderImage,
    altText: "Slide 1",
    caption: "Shop Now",
    header: "Elegance Never Fade",
    key: "1",
  },
  {
    src: SliderImage,
    altText: "Slide 2",
    caption: "Shop Now",
    header: "Elegance Never Fade",
    key: "2",
  },
  {
    src: SliderImage,
    altText: "Slide 3",
    caption: "Shop Now",
    header: "Elegance Never Fade",
    key: "3",
  },
];

const Slider = () => (
  <Row>
    <Col md="12" className="mx-auto slider-style-main">
      <UncontrolledCarousel items={items} />
    </Col>
  </Row>
);

export default Slider;