import "../../assets/css/product-details.css";
import Header from "../../components/Header/navbar";
import DeliverySection from "../../components/Footer/deliverySection";
import Footer from "../../components/Footer/footer";
const ProductDetails = () => {
    return (
        <body className="full-wrapper">
            <Header />
            <div className="container">
            <div class="wrapper row">
            <div className="preview col-md-6">
						
						<div className="preview-pic tab-content">
						  <div className="tab-pane active" id="pic-1"><img src={require("../../assets/img/gallery/10.jpg")} /></div>
						  <div className="tab-pane" id="pic-2"><img src={require("../../assets/img/gallery/11.jpg")} /></div>
						  <div className="tab-pane" id="pic-3"><img src={require("../../assets/img/gallery/9.png")} /></div>
						  <div className="tab-pane" id="pic-4"><img src={require("../../assets/img/gallery/6.jpg")} /></div>
						  <div className="tab-pane" id="pic-5"><img src={require("../../assets/img/gallery/7.jpg")} /></div>
						</div>
						<ul className="preview-thumbnail nav nav-tabs">
						  <li className="active"><a data-target="#pic-1" data-toggle="tab"><img src={require("../../assets/img/gallery/10.jpg")} /></a></li>
						  <li><a data-target="#pic-2" data-toggle="tab"><img src={require("../../assets/img/gallery/11.jpg")} /></a></li>
						  <li><a data-target="#pic-3" data-toggle="tab"><img src={require("../../assets/img/gallery/9.png")} /></a></li>
						  <li><a data-target="#pic-4" data-toggle="tab"><img src={require("../../assets/img/gallery/6.jpg")} /></a></li>
						  <li><a data-target="#pic-5" data-toggle="tab"><img src={require("../../assets/img/gallery/7.jpg")} /></a></li>
						</ul>
						
					</div>
					<div className="details col-md-6">
						<h3 className="product-title">men's shoes fashion</h3>
						<div className="rating">
							<div className="stars">
								<span className="fa fa-star checked"></span>
								<span className="fa fa-star checked"></span>
								<span className="fa fa-star checked"></span>
								<span className="fa fa-star"></span>
								<span className="fa fa-star"></span>
							</div>
							<span className="review-no">41 reviews</span>
						</div>
						<p className="product-description">Suspendisse quos? Tempus cras iure temporibus? Eu laudantium cubilia sem sem! Repudiandae et! Massa senectus enim minim sociosqu delectus posuere.</p>
						<h4 className="price">current price: <span> &#x20b9; 180</span></h4>
						<p className="vote"><strong>91%</strong> of buyers enjoyed this product! <strong>(87 votes)</strong></p>
						<h5 className="sizes">sizes:
							<span className="size" data-toggle="tooltip" title="small">s</span>
							<span className="size" data-toggle="tooltip" title="medium">m</span>
							<span className="size" data-toggle="tooltip" title="large">l</span>
							<span className="size" data-toggle="tooltip" title="xtra large">xl</span>
						</h5>
						<h5 className="colors">colors:
							<span className="color orange not-available" data-toggle="tooltip" title="Not In store"></span>
							<span className="color green"></span>
							<span className="color blue"></span>
						</h5>
						<div className="action">
							<button className="add-to-cart btn btn-default" type="button">add to cart</button>
							<button className="like btn btn-default" type="button"><span className="fa fa-heart"></span></button>
						</div>
					</div>
                    </div>
            </div>
            <DeliverySection />
            <Footer />
        </body>
    )
}

export default ProductDetails;